<template>
  <div class="table">
    <a-list
    :grid="{ gutter: 16, column: 3 }"
    :dataSource="data"
  >
    <a-list-item slot="renderItem" slot-scope="item, index">
      <a-card :title="item.title">{{item.value}}</a-card>
    </a-list-item>
    </a-list>
    <a-table bordered :dataSource="dataList" size="middle" :pagination="pageObj">
      <a-table-column
      title="序号"
      dataIndex="index"
      align = 'center'
      width = '50px'
      key="index"
      />
      <a-table-column
        title="收款地址"
        dataIndex="to_address"
        key="to_address"
        align = 'center'
        width = '390px'
      >
      <template slot-scope="text,record">
          <span :class="!record.error_address ? '' : step === 1 ? 'red' : 'gray'">
            {{text}}
          </span>
        </template>
      </a-table-column>
      <a-table-column
        title="转账数量"
        dataIndex="amount"
        key="amount"
        align = 'center'
        width = '120px'
      >
      <template slot-scope="text,record">
          <span :class="!record.error_amount ? '' : step === 1 ? 'red' : 'gray'">
            {{text}}
          </span>
        </template>
      </a-table-column>
      <a-table-column
        title="备注"
        dataIndex="tag"
        key="tag"
        align = 'center'
        width = '93px'
      >
      </a-table-column>
    </a-table>
  </div>
</template>
<script>

import {add} from '../config/utils.js'

export default {
  props:['dataSource', 'step', 'currency'],
  data () {
    return {
      dataList: [],
      data: [
        {
          title: '转账总数',
          value: 0
        },
        {
          title: '转账明细',
          value: 0
        },
        {
          title: '收款地址数',
          value: 0
        }
      ],
      pageObj: {
        showQuickJumper: true
      },
      addressLenLimit: {
        eth: 42,
        other: 34
      }
    }
  },
  watch: {
    dataSource: function (v) {
      this.dataListHandle(v)
    },
    currency: function (v) {
      this.dataListHandle(this.dataSource)
    }
  },
  methods: {
    dataListHandle (list) {
      let sum = 0
      const errList = []
      this.dataList = list.map((v, i) => {
        const address = v.to_address
        const amount = v.amount[0]
        let error_address = false
        let error_amount = false
        const currency = this.currency.toLowerCase()
        if (currency === 'eth' || currency === 'erc20') {
          if (address.length !== this.addressLenLimit.eth) {
            error_address = true
          }
        } else if (this.currency !== '') {
          if (address.length !== this.addressLenLimit.other) {
            error_address = true
          }
        }
        
        if (this.decimalLen(amount) > 8) {
          error_amount = true
        }
        if (error_address || error_amount) {
          errList.push(i + 1)
        }
        sum = add(sum, amount)
        return {
          index: i + 1,
          to_address: v.to_address,
          tag: v.tag,
          amount: this.getFullNum(amount),
          error_address: error_address,
          error_amount: error_amount
        }
      })
      if (errList.length > 0 && this.step === 1) {
        const str = this.errListHandle(errList) + '条数据'
        this.$notification['error']({
          message: '转账信息异常',
          description: '请核查第' + str,
        })
        this.$emit('errorList', errList)
      }
      this.data.forEach(v => {
        if (v.title === '转账总数') {
          v.value = this.sumHandle(sum)
        } else {
          v.value = list.length
        }
      })
    },
    getFullNum (num) {
      const str = ''+num
      if(!/e/i.test(str)){return num}
  
      return (num).toFixed(18).replace(/\.?0+$/, "")
    },
    decimalLen (num) {
      const str = num + ''
      const index = str.indexOf('.')
      if (index !== -1) {
        return str.split('.')[1].length
      }
      return 0
    },
    errListHandle (errList) {
      const len = errList.length
      let i = 1
      let tmp1 = errList[0]
      let tmp2 = tmp1
      const tmpArr = []
      for (; i < len; i++) {
        tmp2++
        if (tmp2 < errList[i]) {
          if (--tmp2 === tmp1) {
            tmpArr.push(tmp1)
          } else {
            tmpArr.push(tmp1 + ' ~ ' + tmp2)
          }
            tmp1 = errList[i]
            tmp2 = tmp1
        }
      }
      tmpArr.push(tmp1 === tmp2 ? tmp1 : (tmp1 + ' ~ ' + tmp2))
      return tmpArr.join('、')
    },
    sumHandle (sum) {
      const str = sum + ''
      const index = str.indexOf('.')
      if (index !== -1) {
        const tmpStr = str.substring(index, str.length)
        if (tmpStr.length > 8) {
          return (sum - 0).toFixed(8)
        }
      }
      return sum
    },
  }
}
</script>
