<template>
  <div>
    <div class="qrcode" @click="close" v-if="isShowCode">
      <div class="qrcode-content">
        <p>请打开APP端的“验签转账申请”功能 扫描以下二维码，进行数据验签</p>
        <div class="qrcode-img">
          <p @click.stop="refresh" class="refresh" v-if="timeOut">二维码已失效<br>点击刷新</p>
          <canvas ref="img"></canvas>
        </div>
      </div>
    </div>
    <div class="transfer-info">
      <ul>
        <li>
          <span class="label">币种：</span><span>{{currencyName}}</span>
        </li>
        <li>
          <span class="label">审批流：</span><span>{{template}}</span>
        </li>
        <li>
          <span class="label">申请理由：</span><span>{{reason}}</span>
        </li>
        <li>
          <span class="label">截止时间：</span><span>{{dateTime}}</span>
        </li>
        <li>
          <span class="label">矿工费：</span><span>{{miner}}</span>
        </li>
        <li class="clear">
          <span class="label tansfer-list">转账明细：</span>
          <div class="data-list">
            <DataList :dataSource="transferList" :step="2" :currency="currency"/>
          </div>
        </li>
        <li class="clear item-code">
          <span class="label fl">验证码：</span>
          <div class="code fl clear">
            <a-input class="fl input-code" v-model="code"/>
            <s-identify :identifyCode="identifyCode" class="fl code-img"></s-identify>
            <span @click="refreshCode" class="fl code-refresh">看不清，换一张</span>
          </div>
        </li>
      </ul>
      <div class="btn">
        <a-button type='primary' htmlType='submit' class="next" @click="submitTransfer">
          提交
        </a-button>
        <a-button htmlType='submit' @click="upHandle">
          上一步
        </a-button>
      </div>
    </div>
  </div>
</template>

<script>
  import DataList from './DataList'
  import SIdentify from './Identify.vue'
  import QRCode from 'qrcode'
  import AESCrypto from '../config/AESCrypto.js'
  import { transferCommit, getStatus } from '../config/getData.js'

  export default {
    data () {
      return {
        identifyCodes: '1234567890abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ',
        identifyCode: '',
        transferList: [],
        currencyName: '',
        template: '',
        reason: '',
        dateTime: '',
        miner: '',
        msg: null,
        isShowCode: false,
        timeOut: false,
        timer: null,
        timerInterval: null,
        code: '',
        currency: ''
      }
    },
    mounted () {
      this.identifyCode = ''
      this.makeCode(this.identifyCodes, 4)
      this.getData()
    },
    methods: {
    // 生成一个随机数
      getData () {
        const data = JSON.parse(window.localStorage.getItem('msg'))
        this.currency = data.currency
        this.currencyName = data.coin_name + '(' + data.coin_fullname + ')'
        this.template = data.t_name
        this.reason = data.reason
        this.dateTime = data.deadline
        const miner = data.miner
        if (miner === 1) {
          this.miner = '最低'
        } else {
          this.miner = miner + '倍'
        }
        this.transferList = data.applys
        // delete data.template_name
        this.msg = data
      },
      randomNum (min, max) {
        return Math.floor(Math.random() * (max - min) + min)
      },
      refreshCode () {
        this.identifyCode = ''
        this.makeCode(this.identifyCodes, 4)
      },
      makeCode (o, l) {
        for (let i = 0; i < l; i++) {
          this.identifyCode += o[this.randomNum(0, o.length)]
        }
      },
      submitTransfer () {
        if (this.code.toLowerCase() !== this.identifyCode.toLowerCase()) {
          this.$notification['error']({
            message: '验证码错误',
            description: '请重新提交',
          })
          this.identifyCode = ''
          this.code = ''
          this.makeCode(this.identifyCodes, 4)
          return
        }
        this.transferCommit()
      },
      async transferCommit () {
        const id = this.guid()
        const aesKey = this.aesKey()
        const msg = JSON.stringify(this.msg)
        const msgCrypt = AESCrypto.encrypt(msg, aesKey)
        const res = await transferCommit({
          id: id,
          msg: msgCrypt
        })
        if (res.Code === 0) {      
          this.isShowCode = true
          const qrCodeContent = JSON.stringify({
            id: id,
            aesKey: aesKey
          })
          this.$nextTick(function () {
            const canvas = this.$refs['img']
            QRCode.toCanvas(canvas, qrCodeContent, {
              colorDark: '#000000',
              colorLight: '#ffffff',
              scale: 5
            }, function (error) {
              if (error) console.error(error)
            })
          })
          this.identifyCode = ''
          this.makeCode(this.identifyCodes, 4)
          // if(this.timerInterval) clearInterval(this.timerInterval)
          this.timerInterval = setInterval(() => {
            this.getStatus(id)
          }, 1000)
          // if (this.timer) clearTimeout(this.timer)
          this.timer = setTimeout(() => {
            this.timeOut = true
            clearInterval(this.timerInterval)
          }, 60000)
          
        } else {
          this.$notification['error']({
            message: '错误 Code:' + res.Code,
            description: '请尝试重新提交',
          })
        }
      },
      async getStatus (id) {
        const res = await getStatus({id: id})
        if (res.Code === 0) {
          const data = res.Data
          if (data.status === 2) {
            this.clearTimer()
            this.$success({
            title: '批量转账申请提交成功！',
            content: '',
            onOk () {
              window.location.reload()
            }
            })
          } else if (data.status === 3) {
            this.timeOut = true
            this.clearTimer()
          }
        }
      },
      upHandle () {
        this.$emit('getStatus', true)
      },
      close () {
        this.isShowCode = false
        this.timeOut = false
        this.clearTimer()
      },
      refresh () {
        this.timeOut = false
        this.clearTimer()
        this.transferCommit()
      },
      clearTimer () {
        clearInterval(this.timerInterval)
        clearTimeout(this.timer)
      },
      S4 () {
        return (((1+Math.random())*0x10000)|0).toString(16).substring(1)
      },
      aesKey () {
        const S4 = this.S4
        return (S4()+S4()+S4()+S4())
      },
      guid() {
        const S4 = this.S4
        return (S4()+S4()+"-"+S4()+"-"+S4()+"-"+S4()+"-"+S4()+S4()+S4())
      }
    },
    components: {
      DataList, SIdentify
    }
  }
</script>

