<template>
    <div class="contain">
        <a-steps :current="current">
          <a-step :key="s.index" :title="s.title" v-for="s in steps" size="small"></a-step>
        </a-steps>
        <div class="form-content">
          <InputTransfer v-show="inputStatus" v-on:getStatus="getStatus"/>
          <ViewTransfer v-if="!inputStatus" v-on:getStatus="getStatus"/>
        </div>
    </div>
</template>
<script>
import InputTransfer from './InputTransfer'
import ViewTransfer from './ViewTransfer'
export default {
  data() {
      return {
          inputStatus: true,
          current: 0,
          steps: [
              {
                  title: "填写转账信息",
                  index: 0
              },
              {
                  title: "确认转账信息",
                  index: 1
              },
              {
                  title: "完成",
                  index: 2
              }
          ],
      }
  },
  methods: {
    getStatus (params) {
      this.current = params ? 0 : 1
      this.inputStatus = params
    }
  },
  components: {
    InputTransfer, ViewTransfer
  }
}
</script>

<style lang="less" scoped>
  .contain {
    padding: 50px 225px;
    background: #fff;
  }
  .form-content {
    padding-top: 40px;
  }
</style>
