import fetch from './fetch'

// 获取币种
export const getCoins = data => fetch('/api/v1/webtransfer/getcoins', data, '获取币种列表失败，请尝试刷新页面')

// 获取审批流
export const getTemplates = data => fetch('/api/v1/webtransfer/gettemplate', data, '获取审批流列表失败, 请尝试重新选择币种')

// 提交转账信息
export const transferCommit = data => fetch('/api/v1/webtransfer/transfercommit', data, '请重新提交转账信息', 'POST')

// web查询提交状态
export const getStatus = data => fetch('/api/v1/webtransfer/getcommitstatus', data)