<template>
<div>
  <a-form>
    <a-form-item
      label='币种'
      :labelCol="{ span: 5 }"
      :wrapperCol="{ span: 12 }"
    >
      <a-select
        placeholder='请选择'
        :allowClear='true'
        :value='currencySelect'
        @change="this.currencySelectChange"
      >
        <a-select-option v-for="(item, index) in currencyList" :value='index' :key="index">{{item.Name + '(' + item.FullName + ')'}}</a-select-option>
      </a-select>
      <span class="blance" v-if="templateSelect !== undefined">剩余额度：{{balance}}</span>
    </a-form-item>
    <a-form-item
      label='审批流'
      :labelCol="{ span: 5 }"
      :wrapperCol="{ span: 12 }"
    >
      <a-select
        placeholder='请选择'
        :allowClear='true'
        :value='templateSelect'
        @change="this.templateSelectChange"
      >
        <a-select-option v-for="(item,index) in templateList" :key="index" :value='index'>{{item.Name}}</a-select-option>
      </a-select>
    </a-form-item>
    <a-form-item
      :labelCol="{ span: 5 }"
      :wrapperCol="{ span: 12 }"
      label='申请理由'
    >
      <a-textarea placeholder="请输入200字以内的申请理由…" :rows="4" v-model='reason'/>
    </a-form-item>
    <a-form-item 
      label='截止时间（选填）'
      :labelCol="{ span: 5 }"
      :wrapperCol="{ span: 12 }">
      <a-col :span="11">
        <a-form-item>
          <a-date-picker style="width: 100%" placeholder="选择日期" v-model='date'/>
        </a-form-item>
      </a-col>
      <a-col :span="2">
        <span :style="{ display: 'inline-block', width: '100%', textAlign: 'center' }">
        </span>
      </a-col>
      <a-col :span="11">
        <a-form-item>
          <a-time-picker style="width: 100%" placeholder="选择时间" v-model='time'/>
        </a-form-item>
      </a-col>
    </a-form-item>
    <a-form-item
      label='矿工费'
      :labelCol="{ span: 5 }"
      :wrapperCol="{ span: 16 }"
    >
       <a-radio-group name="radioGroup" :defaultValue="1" size="small" v-model="miner">
        <a-radio :value="1">最低</a-radio>
        <a-radio :value="2">2倍</a-radio>
        <a-radio :value="3">3倍</a-radio>
        <a-radio :value="4">4倍</a-radio>
        <a-radio :value="5">5倍</a-radio>
        <a-radio :value="10">10倍</a-radio>
        <!-- <a-radio :value="20">20倍</a-radio> -->
      </a-radio-group>
    </a-form-item>
    <a-form-item
      label='转账信息'
      :labelCol="{ span: 5 }"
      :wrapperCol="{ span: 18 }"
      class="transfer-info"
    >
      <input type="file" @input="handleChange" id="up-file" ref="file_up" :value="fileValue" accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel">
      <a-button @click="upFile">
        <a-icon type="upload" />  {{uploadText}}
      </a-button>
      <div class="progress" v-show="uploadFile">
        <div class="file-name">
          <span>{{fileName}}</span>
        </div>
        <div class="file-progress">
          <a-progress :percent="percent" size="small" :status="status" />
        </div>
      </div>
      <div class="tips"><span>只能上传xls/xlsx格式文件，上限500条记录。</span><a href="javascript:;" class="download" @click="outputXlsxFile([['收款地址', '转账数量', '备注']], [{wch: 50}, {wch: 10}, {wch: 30}], '转账模板')">下载转账模板</a></div>
      <DataList v-if="uploadData" :dataSource="transferList" :step="1" :currency="currency" @errorList="getErrorList"/>
    </a-form-item>
  </a-form>
    <div class="submitBtn">
      <a-button type='primary' htmlType='submit' class="next" @click="nextHandle">
          下一步
        </a-button>
        <a-button htmlType='submit' @click="clearHandle">
          清空
        </a-button>
    </div>
  </div>
</template>

<script>
import XLSX from 'xlsx'
import DataList from './DataList'
import {getCoins, getTemplates} from '../config/getData.js'
import moment from 'moment'
import {add} from '../config/utils.js'
export default {
  data () {
    return {
      json_fields: {
        address: '收款地址',
        amount: '转账数量',
        mark: '备注'
      },
      json_data: [],
      json_meta: [
            [
                {
                    'key': 'charset',
                    'value': 'utf-8'
                }
            ]
        ],
      formLayout: 'horizontal',
      balance: 0,
      miner: 1,
      percent: 0,
      status: 'active',
      fileName: '',
      uploadData: false,
      uploadFile: false,
      uploadText: '上传',
      date: null,
      time: null,
      reason: undefined,
      fileValue: null,
      currencyList: [],
      templateList: [],
      currencySelect: undefined,
      templateSelect: undefined,
      transferList: [],
      transferSum: 0,
      currency: '',
      errorList: []
    }
  },
  components: {
    DataList
  },
  created () {
    this.getCoins()
  },
  methods: {
    currencySelectChange (value) {
      this.currencySelect = value
      this.templateList = []
      this.templateSelect = undefined
      const item = this.currencyList[value]
      const coinId = item.ID
      this.currency = item.Currency
      this.getTemplates(coinId)
    },
    templateSelectChange (value) {
      this.templateSelect = value
      this.balance = this.templateList[value].ReferAmount
    },
    getErrorList (params) {
      this.errorList = params
    },
    handleChange (value) {
      const file = value.target.files[0]
      if (file) {
        this.uploadFile = true
        this.percent = 0
        this.uploadText = '重新上传'
        this.status = 'active'
        this.fileName = file.name
        const readerFile = new FileReader()
        readerFile.onload = (e) => {
          const data = e.target.result
          try {
            const wb = XLSX.read(data, { type: "binary" })
            const dataList = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]])
            let sum = 0
            const len = dataList.length
            if (len > 500) {
                this.$message.error('最多只能上传500条数据，请重新上传')
                this.status = 'exception'
                this.uploadData = false
                return
            } else if (len !== 0) {
              const keys = Object.keys(dataList[0]).join('')
              if (keys.indexOf('收款地址') === -1) {
                this.$message.error('表格内容格式错误，请重新上传')
                this.status = 'exception'
                this.uploadData = false
                return
              }
            } else {
              this.$message.error('表格内容为空，请重新上传')
              this.status = 'exception'
              this.uploadData = false
              return
            }
            this.transferList = dataList.map((v, i) => {
              const amount = v['转账数量']
              if (!this.isNum(amount - 0) || amount < 0.00000001) {
                throw '表格第' + (i + 1) + '条数据错误(上传文件中，转账数量不能为空、0、非数字或小于0.00000001)'
              }
              sum = add(sum, amount)
              return {
                to_address: v['收款地址'] || '',
                tag: v['备注'],
                amount: [amount]
              }
            })
            
            this.transferSum = this.sumHandle(sum)
          } catch (error) {
            if (this.isString(error)) {
              this.$message.error(error)
            } else {
              this.$message.error('表格格式或内容错误（只能上传xls/xlsx格式文件，上限500条记录。）')
            }
            
            this.status = 'exception'
            this.uploadData = false
          }
        }
        readerFile.onprogress = (e) => {
          this.percent = parseInt((e.loaded / e.total) * 100)
          if (this.percent >= 100) {
            this.uploadData = true
            this.status = 'success'
          }
        }
        readerFile.readAsBinaryString(file)
      }
    },
    isString(str){ 
      return (typeof str=='string')&&str.constructor==String; 
    },
    isNum (num) {
      if(num === "" || num === null || typeof num !== 'number'){
        return false
      }
      if (!isNaN(num)) {
        return true
      } else {
        return false
      }
    },
    upFile () {
      this.$refs['file_up'].click()
    },
    outputXlsxFile (data, wscols, xlsxName) {
      /* convert state to workbook */
      const ws = XLSX.utils.aoa_to_sheet(data)
      ws['!cols'] = wscols
      const wb = XLSX.utils.book_new()
      XLSX.utils.book_append_sheet(wb, ws, xlsxName)
      /* generate file and send to client */
      XLSX.writeFile(wb, xlsxName + ".xlsx")
    },
    nextHandle () {
      const msg = {}
      const currencyIndex = this.currencySelect
      const templateIndex = this.templateSelect

      const currencyInfo = this.currencyList[currencyIndex]
      const templateInfo = this.templateList[templateIndex]
    
      if (currencyIndex === undefined) {
        this.$message.error('请选择币种')
        return
      }
      if (templateIndex === undefined) {
        this.$message.error('请选择审批流')
        return
      }
      const reason = this.reason
      if (reason === undefined || reason.trim() === '') {
        this.$message.error('请填写申请理由')
        return
      }
      const date = this.date
      const time = this.time
      let deadline
      if (date === null || time === null) {
         deadline = ''
      } else {
        deadline = moment(this.date).format('YYYY-MM-DD') + ' ' + moment(this.time).format('HH:mm:ss')
      }
      if (deadline !== '' && (moment(deadline).valueOf() - (+new Date())) <= 0) {
        this.$message.error('截止时间不能早于当前时间，请修改后重试')
        return
      }
      const dataList = this.transferList
      if (dataList.length === 0) {
        this.$message.error('请上传转账信息（只能上传xls/xlsx格式文件，上限500条记录。）')
        return
      }
      if ((this.transferSum - 0) > (this.balance - 0)) {
        this.$message.error('剩余额度不足，无法提交申请！')
        return
      }
      if (this.errorList.length > 0) {
        this.$message.error('表格内容错误，无法提交申请！')
        return
      }
      msg.currency = currencyInfo.Currency
      msg.coin_name = currencyInfo.Name
      msg.coin_fullname = currencyInfo.FullName
      msg.precise = currencyInfo.Precise
      msg.token = currencyInfo.TokenAddress
      msg.t_hash = templateInfo.Hash
      msg.reason = reason
      msg.miner = this.miner
      msg.t_name = templateInfo.Name
      msg.timestamp = +new Date()
      msg.coin_id = currencyInfo.ID
      msg.amount = this.transferSum
      msg.applys = this.transferList
      msg.deadline = deadline
      window.localStorage.setItem('msg', JSON.stringify(msg))
      this.$emit('getStatus', false)
    },
    decimalLen (num) {
      const str = num + ''
      const index = str.indexOf('.')
      if (index !== -1) {
        return str.split('.')[1].length
      }
      return 0
    },
    clearHandle () {
      this.currencySelect = undefined
      this.templateSelect = undefined
      this.reason = undefined
      this.date = undefined
      this.time = undefined
      this.miner = 1
      this.uploadFile = false
      this.uploadData = false
      this.uploadText = '上传'
      this.currency = ''
    },
    sumHandle (sum) {
      const str = sum + ''
      const index = str.indexOf('.')
      if (index !== -1) {
        const tmpStr = str.substring(index, str.length)
        if (tmpStr.length > 8) {
          return (sum - 0).toFixed(8)
        }
      }
      return sum
    },
    async getCoins () {
      const res = await getCoins()
      if (res.Code === 0) {
        this.currencyList = res.Data
      }
    },
    async getTemplates (id) {
      const res = await getTemplates({coin_id: id})
      if (res.Code === 0) {
        this.templateList = res.Data
      }
    }
  },
}
</script>
<style lang="less">
#up-file {
  display: none;
}
.progress {
  display: inline-block;
  position: absolute;
  top: -16px;
  width: 125px;
  margin-left: 18px;
}
.file-name {
  font-size: 12px;
  height: 12px;
}
.transfer-info {
  position: relative;
}
.tips {
  padding-top: 5px;
  font-size:14px;
  font-family:PingFangSC-Regular;
  font-weight:400;
  color:rgba(140,140,140,1);
  line-height:20px;
  padding-bottom: 20px;
}
.next {
  margin-right: 10px;
}
.submitBtn {
  margin-left: 155px;
}
.blance {
  position: absolute;
  left: 0;
  bottom: -40px;
}
</style>
